import React from "react";
import "./Tab.scss";

const Tab = ({ activeTab, setActiveTab, label, size }) => {
	const handleClick = () => {
		setActiveTab(label);
	};

	let className = `tab_listitem_${size}`;

	if (activeTab === label) {
		className += "_active";
	}
	return (
		<div>
			<li className={className} onClick={handleClick}>
				<div className={className + "_label"}>{label}</div>
			</li>
		</div>
	);
};

export default Tab;
